import React,{Component} from 'react'
import { Row, Col } from 'antd';

class Frame extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Row className="projectFrame">
                {this.props.children}
            </Row>
        )
    }
}

module.exports = Frame;


